@import "ScssHelpers/helpers-all.scss";

.woocommerce-MyAccount-navigation {
	@include bp($point_2, min) {
		margin-right: 8.5rem;
		width: 33.1rem;
	}
	@include bp($point_3) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.woocommerce-MyAccount-navigation-link {
	position: relative;
	padding: 1.9rem 0 2rem;
	border-bottom: 1px solid $beige_2;
}

.woocommerce-MyAccount-navigation-link.is-active:before {
	content: "";
	position: absolute;
	left: -2.6rem;
	right: -2.6rem;
	top: 0;
	bottom: -1px;
	background-color: $gray_3;
	z-index: 1;
}

.woocommerce-MyAccount-navigation-link a {
	position: relative;
	display: block;

	line-height: 3.2rem;
	color: $black;
	z-index: 2;
	transition: color 0.2s ease;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $gold_2;
	}

	&:before {
		content: "";
		position: absolute;
		right: 0;
		top: 50%;
		display: flex;
		align-items: center;
		justify-content: center;

		border-radius: 100%;
		border: 1px solid $gold_2;
		transform: translateY(-50%);

		@include bp($point_2, min) {
			width: 3.2rem;
			height: 3.2rem;
		}
		@include bp($point_3) {
			width: 2.6rem;
			height: 2.6rem;
		}
	}

	@include bp($point_2, min) {
		font-size: 2.2rem;
	}
	@include bp($point_3) {
		font-size: 1.8rem;
	}
}

.woocommerce-MyAccount-navigation-link.is-active a {
	font-weight: 500;

	&:hover,
	&:focus {
		color: $black;
	}
}

thead {
	.shop_table_responsive & {
		@include bp($point_2, min) {
			display: table-header-group;
		}
		@include bp($point_3) {
			display: none;
		}
	}
}

td {
	.shop_table_responsive & {
		@include bp($point_3) {
			display: block;
			text-align: right;
			clear: both;

			&::before {
				content: attr(data-title) ": ";
				display: table;
				font-weight: 500;
				float: left;
			}
		}
	}
}
