@import "ScssHelpers/helpers-all.scss";

.account_content {
	padding-top: 5.4rem;
}

.account_content .woocommerce {
	@include bp($point_2, min) {
		display: flex;
		align-items: flex-start;
		flex-wrap: wrap;
	}
}

.account_content .woocommerce-MyAccount-content {
	font-size: 1.6rem;

	@include bp($point_2, min) {
		width: calc(100% - 41.6rem);
	}
}

.account_content .woocommerce-MyAccount-content > p {
	margin-bottom: 1.2rem;
	line-height: 1.5;

	&:first-child {
		padding-top: 0.5rem;
		font-size: 1.8rem;

		b,
		strong {
			font-size: 2.2rem;
		}
	}
}

.account_content .woocommerce-MyAccount-content p b,
.account_content .woocommerce-MyAccount-content p strong {
	font-weight: 500;
}

.account_content .woocommerce-MyAccount-content p a {
	color: $gold_2;
	text-decoration: underline;
	font-weight: 500;

	&:focus,
	&:hover {
		text-decoration: none;
	}
}

.account_content .button {
	@include btn_color_gold;
	@include btn_default_size;
}
