@import "ScssHelpers/helpers-all.scss";

.account_content .edit-account {
	display: flex;
	flex-wrap: wrap;
	padding-top: 3.2rem;
	border-top: 1px solid $beige_2;

	&:not(:first-child) {
		margin-top: 4rem;
	}
}

.form-row {
	.account_content .woocommerce form & {
		margin-bottom: 2.4rem;
	}

	.account_content fieldset & {
		@include bp($point_2, min) {
			max-width: 71.2rem;
		}
	}

	input.input-text,
	textarea {
		.account_content .woocommerce form & {
			@include form_el_default;
		}
	}

	label {
		.account_content .woocommerce form & {
			@include form_el_dark_label;
		}
	}

	em {
		.account_content .woocommerce form & {
			font-size: 1.4rem;
		}
	}
}

.form-row-first,
.form-row-last {
	.account_content .woocommerce form & {
		@include bp($point_4, min) {
			width: calc(50% - 1.2rem);
		}

		@include bp($point_4 - 1) {
			width: 100%;
		}
	}
}

.form-row-first {
	.account_content .woocommerce form & {
		@include bp($point_4, min) {
			margin-right: 2.4rem;
		}
	}
}

.form-row-wide {
	.account_content .woocommerce form & {
		width: 100%;
	}
}

.account_content fieldset {
	width: 100%;
	padding-top: 3.8rem;
	margin-top: 2.9rem;
	border-top: 1px solid $beige_2;
}

.account_content fieldset legend {
	float: left;
	width: 100%;
	margin-bottom: 2.4rem;
	font-size: 1.8rem;
	letter-spacing: 2px;
	color: #868686;
	text-transform: uppercase;
}

.account_content .woocommerce form .form-row .password-input {
	position: relative;
	display: block;
}

.account_content .woocommerce form .form-row .show-password-input {
	position: absolute;
	right: 0;
	top: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	width: $input_height;
	height: $input_height;
	cursor: pointer;

	&:after {
		content: "";
		width: 1.7rem;
		height: 1.2rem;
		display: block;
		background-image: url("data:image/svg+xml;base64,");
		background-repeat: no-repeat;
		background-position: center;
	}
}

.account_content .woocommerce form .form-row .show-password-input.display-password {
	opacity: 0.4;
}
